import {Component, OnInit, OnDestroy} from "@angular/core";
import {Router, ActivatedRoute} from "@angular/router";
import { Subscription } from 'rxjs/Subscription';

import { Account } from '../../account/account.model';
import { Doctor, AccountStatus } from '../admin.model';
import { AdminService } from '../admin.service';
import { DOMHelper } from "../../shared/helpers";

@Component({
    selector: 'admin-doctor-details',
    template: require('./doctor-details.component.html'),
    styles: [
        require('../../../sass/pages/doctor-details.scss')
    ],
})
export class DoctorDetailsComponent implements OnInit, OnDestroy {
    private account: Account;
    private sub: Subscription;

    private doctor: Doctor = new Doctor();

    private userDataLoading = false;
    private updatingStatus = false;

    constructor(private router: Router,
        private route: ActivatedRoute,
        private adminService: AdminService) { }

    ngOnInit() {
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (!this.account) {
            this.router.navigate(['/signin']);
        } else {
            DOMHelper.scrollUpTo(document.getElementsByTagName('body')[0], 0, 600);
            this.sub = this.route.params.subscribe(params => {
                let id = params['drid'];
                this.getDoctorProfile(id);
            });
        }

    }

    ngOnDestroy() {
        this.sub.unsubscribe();
    }


    private getDoctorProfile(id: string) {
        let getDoctorsSub: Subscription;
        let getDoctorsTO = setTimeout(() => {
            if (this.userDataLoading && getDoctorsSub && !getDoctorsSub.isUnsubscribed) {
                getDoctorsSub.unsubscribe();
                this.userDataLoading = false;
                this.showFlashError();
            }
        }, 5000);

        this.userDataLoading = true;
        getDoctorsSub = this.adminService.getDoctor(this.account, id)
            .subscribe(
            success => {
                clearTimeout(getDoctorsTO);
                this.userDataLoading = false;
                if (success.status == 200) {
                    this.doctor = success.data;

                    (<HTMLLinkElement>document.getElementById('link-download')).href = this.doctor.attachment;
                } else {
                    console.log("Error in suc" + success);
                }
            },
            error => {
                clearTimeout(getDoctorsTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    if (error.status == 404) {
                        this.router.navigate(['/404']);
                    } else {
                        console.log("Error" + error);
                    }
                }
                this.userDataLoading = false;
            }
            );
    }

    changeDoctorStatus(status: number) {
        let statSub: Subscription;
        let statTO = setTimeout(() => {
            if (this.updatingStatus && statSub && !statSub.isUnsubscribed) {
                statSub.unsubscribe();
                this.updatingStatus = false;
                this.showFlashError();
            }
        }, 5000);

        let newStat = this.doctor.status == AccountStatus.ACTIVE ? AccountStatus.DISABLED : AccountStatus.ACTIVE;

        this.updatingStatus = true;
        statSub = this.adminService.changeStatus(this.account, this.doctor.id, newStat)
            .subscribe(
            success => {
                clearTimeout(statTO);
                this.updatingStatus = false;
                if (success.status == 200) {
                    this.doctor.status = newStat;
                } else {
                    console.log("E i s" + success.errorMsg);
                }
            },
            error => {
                clearTimeout(statTO);
                this.updatingStatus = false;
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
            );

    }

    // Model
    windowClickModalHandler(event: any) {
        if (event.target.id == 'imgviewer-desc') {
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            let placeModal = document.getElementById('imgviewer-desc');
            placeModal.style.display = 'none';
            placeModal.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickModalHandler);
        }
    }

    openImageViewer(base64: string) {
        let placeModel = document.getElementById('imgviewer-desc');
        if (placeModel.getAttribute('data-isopened') == '0') {
            (<HTMLImageElement>document.getElementById('imgviewer-img')).src = base64;
            document.getElementsByTagName('body')[0].style.overflow = 'hidden';
            placeModel.style.display = 'flex';
            placeModel.setAttribute('data-isopened', '1');
            window.addEventListener('click', this.windowClickModalHandler);
        }
    }

    closeImageViewer() {
        let placeModel = document.getElementById('imgviewer-desc');
        if (placeModel.getAttribute('data-isopened') == '1') {
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            placeModel.style.display = 'none';
            placeModel.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickModalHandler);
        }
    }
    // End modal

    showFlashError() {
        let item = document.getElementById('flash-noti-error');
        item.style.display = 'block';
        setTimeout(() => {
            item.style.display = 'none';
        }, 4000);
    }

}


