import {Injectable} from "@angular/core";
import {Http, Response, Headers, RequestOptions} from '@angular/http';
import {Observable} from 'rxjs/Observable';
import * as moment from "moment";
import * as lodash from 'lodash';

import {Account, SigninModel} from "./account.model";
import {SingleOutputModel} from "../shared/output.model";
import Logger from "../shared/logger.service";
import { ROOT_URL } from "../shared/constants";

@Injectable()
export class AccountService {
    private SIGN_IN_URL = ROOT_URL + "/api/admin/login";

    constructor(private http: Http) { }

    signIn(user: SigninModel) {
        let body = JSON.stringify({
            Email: user.email,
            Password: user.password
        });
        let headers = new Headers({
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json',
            'Access-Control-Allow-Origin': '*'
        });
        let options = new RequestOptions({ headers: headers });

        return this.http.post(this.SIGN_IN_URL, body, options)
            .map(this.extractSigninData)
            .catch(this.handlerSigninError);
    }

    private extractSigninData(res: Response) {
        let data: Account = new Account();
        if (res.status == 200) {
            let userBody = res.json().User;
            data.id = userBody.Id;
            data.firstName = userBody.FirstName;
            data.lastName = userBody.LastName;
            data.fullName = data.firstName + ' ' + data.lastName;
            data.email = userBody.Email;
            data.password = userBody.Password;
            data.phone = userBody.Phone;
            data.sex = userBody.Gender;
            data.role = userBody.Role;
            data.created = userBody.Created;
            data.lastModified = userBody.LastModified;
            data.status = userBody.Status;
            data.address = userBody.Address;
            data.photo = userBody.Photo;

        }
        return new SingleOutputModel(res.status, data, null);
    }

    private handlerSigninError(error: any) {
        let errMsg: string;
        if (error.message) {
            errMsg = error.message;
        } else if (error.status) {
            errMsg = `${error.status} - ${error.statusText} - ${JSON.stringify(error)}`;
        } else {
            errMsg = 'Server error';
        }

        console.log(errMsg);
        return Observable.throw(error);
    }
}




