import { Injectable } from "@angular/core";
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import * as moment from "moment";

import { Doctor, Patient, Place, MedicalCategory, StatusStatistic, AccountRole, AccountStatus } from "./admin.model";
import { ISO8601DateFormat } from "../shared/constants";
import { SingleOutputModel, ArrayOutputModel } from "../shared/output.model";
import { Account } from '../account/account.model';
import { ROOT_URL } from "../shared/constants";

@Injectable()
export class AdminService {
    private URL_DOCTOR = ROOT_URL + '/api/doctor';
    private URL_DOCTOR_FILTER = ROOT_URL + '/api/doctor/filter';
    private URL_PATIENT = ROOT_URL + '/api/patient';
    private URL_PATIENT_FILTER = ROOT_URL + '/api/patient/filter';
    private URL_PLACE = ROOT_URL + '/api/place';
    private URL_PLACE_FILTER = ROOT_URL + '/api/place/filter';
    private URL_CATEGORY = ROOT_URL + '/api/medical/category';
    private URL_CATEGORY_FILTER = ROOT_URL + '/api/medical/category/filter';
    private URL_PERSON_CHANGE_STATUS = ROOT_URL + '/api/person/status';
    private URL_PERSON_STATISTIC = ROOT_URL + '/api/person/status/statistic';

    constructor(private http: Http) { }

    getDoctor(account: Account, id: string) {
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        });
        let options = new RequestOptions({ headers: headers });
        return this.http.get(this.URL_DOCTOR + `?id=${id}`, options)
            .map(this.extractDataSingleDoctor)
            .catch(this.handlerError);
    }

    filterDoctors(account: Account, name: string, email: string, status: number, pageNo: number, records?: number) {
        // In server: page start from 0, in client, page start from 1
        let obj = {};
        if (pageNo) {
            pageNo = pageNo - 1;
            obj['Page'] = pageNo;
        }
        if (records) {
            obj['Records'] = records;
        }
        if (status && status == -1) {
            // obj['Statuses'] = [AccountStatus.ACTIVE, AccountStatus.DISABLED];
        } else {
            obj['Statuses'] = [parseInt(status+'')];
        }
        if (email) {
            obj['Email'] = email;
        }
        if (name) {
            obj['Name'] = name;
        }
        let body = JSON.stringify(obj);
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        ); 
        let options = new RequestOptions({ headers: headers }); console.log(body);
        return this.http.post(this.URL_DOCTOR_FILTER, body, options)
            .map(this.extractDataMultipleDoctor)
            .catch(this.handlerError);
    }

    getPatient(account: Account, id: string) {
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        });
        let options = new RequestOptions({ headers: headers });
        return this.http.get(this.URL_PATIENT + `?id=${id}`, options)
            .map(this.extractDataSinglePatient)
            .catch(this.handlerError);
    }

    filterPatients(account: Account, name: string, email: string, status: AccountStatus, pageNo: number, records?: number) {
        // In server: page start from 0, in client, page start from 1
        let obj = {}
        if (pageNo) {
            pageNo = pageNo - 1;
            obj['Page'] = pageNo
        }
        if (records) {
            obj['Records'] = records;
        }
        if (status && status != -1) {
            obj['Status'] = status;
        }
        if (email) {
            obj['Email'] = email;
        }
        if (name) {
            obj['Name'] = name;
        }
        let body = JSON.stringify(obj);
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        );
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.URL_PATIENT_FILTER, body, options)
            .map(this.extractDataMultiplePatient)
            .catch(this.handlerError);
    }


    changeStatus(account: Account, id: string, status: number) {
        let body = JSON.stringify({
            Status: status
        });
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        ); 
        let options = new RequestOptions({ headers: headers });
        return this.http.put(this.URL_PERSON_CHANGE_STATUS + `?Id=${id}`, body, options)
            .map(this.extractDataChangeStatus)
            .catch(this.handlerError);
    }

    getAccountStatusStatistic(account: Account, role: AccountRole) {
        let body = JSON.stringify({
            Role: role
        });
        let headers = new Headers({
            'Email': account.email,
            'Password': account.password,
            'Accept-Language': 'en-US',
            'Content-Type': 'application/json'
        }
        );
        let options = new RequestOptions({ headers: headers });
        return this.http.post(this.URL_PERSON_STATISTIC, body, options)
            .map(this.extractDataStatusStatistic)
            .catch(this.handlerError);
    }

    createMedrecordCategory(account: Account, data: MedicalCategory) {
        let headers = new Headers({
            "Accept-Language": "en-US",
            'Content-Type': 'application/json',
            "Email": account.email,
            "Password": account.password
        });
        let options = new RequestOptions({ headers: headers });
        let body = JSON.stringify({
            "Name": data.name
        }); 
        return this.http.post(this.URL_CATEGORY, body, options)
            .map((res: Response) => { return new SingleOutputModel(res.status, null, null) })
            .catch(this.handlerError);
    }

    updateMedrecordCategory(account: Account, data: MedicalCategory) {
        let headers = new Headers({
            "Accept-Language": "en-US",
            'Content-Type': 'application/json',
            "Email": account.email,
            "Password": account.password
        });
        let options = new RequestOptions({ headers: headers });
        let body = JSON.stringify({
            "Name": data.name
        });
        return this.http.put(this.URL_CATEGORY + `?Id=${data.id}`, body, options)
            .map((res: Response) => { return new SingleOutputModel(res.status, null, null) })
            .catch(this.handlerError);
    }

    filterMedrecordCategories(account: Account, name: string, pageNo: number, records?: number) {
        let headers = new Headers({
            "Accept-Language": "en-US",
            'Content-Type': 'application/json',
            "Email": account.email,
            "Password": account.password
        });
        let options = new RequestOptions({ headers: headers });
        let obj = {};
        // In server: page start from 0, in client, page start from 1
        if (pageNo) {
            pageNo = pageNo - 1;
            obj['Page'] = pageNo;
        }
        if (records) {
            obj["Records"] = records;
        }
        if (name) {
            obj['Name'] = name;
        }
        let body = JSON.stringify(obj);
        return this.http.post(this.URL_CATEGORY_FILTER, body, options)
            .map(this.extractDataMultipleMedCategories)
            .catch(this.handlerError);
    }

    createPlace(account: Account, data: Place) {
        let headers = new Headers({
            "Accept-Language": "en-US",
            'Content-Type': 'application/json',
            "Email": account.email,
            "Password": account.password
        });
        let options = new RequestOptions({ headers: headers });
        let body = JSON.stringify({
            "City": data.city,
            "Country": data.country
        });
        return this.http.post(this.URL_PLACE, body, options)
            .map((res: Response) => { return new SingleOutputModel(res.status, null, null) })
            .catch(this.handlerError);
    }

    updatePlace(account: Account, data: Place) {
        let headers = new Headers({
            "Accept-Language": "en-US",
            'Content-Type': 'application/json',
            "Email": account.email,
            "Password": account.password
        });
        let options = new RequestOptions({ headers: headers });
        let body = JSON.stringify({
            "City": data.city,
            "Country": data.country
        });
        return this.http.put(this.URL_PLACE + `?Id=${data.id}`, body, options)
            .map((res: Response) => { return new SingleOutputModel(res.status, null, null) })
            .catch(this.handlerError);
    }

    filterPlaces(account: Account, city: string, country: string, pageNo: number, records?: number) {
        let headers = new Headers({
            "Accept-Language": "en-US",
            'Content-Type': 'application/json',
            "Email": account.email,
            "Password": account.password
        });
        let options = new RequestOptions({ headers: headers });
        let obj = {};
        if (pageNo) {
            pageNo = pageNo - 1;
            obj['Page'] = pageNo;
        }
        if (records) {
            obj['Records'] = records;
        }
        if (city) {
            obj['City'] = city;
        }
        if (country) {
            obj['Country'] = country;
        }
        let body = JSON.stringify(obj); 
        return this.http.post("http://localhost:46170/api/place/filter", body, options)
            .map(this.extractDataMultiplePlaces)
            .catch(this.handlerError);
    }

    private extractDataMultipleMedCategories(res: Response) {
        let data: MedicalCategory[] = [];
        if (res.status == 200) {
            let body = res.json().MedicalCategories;
            for (let item of body) {
                let medcate = new MedicalCategory();
                medcate.id = item.Id;
                medcate.name = item.Name;
                medcate.created = moment.utc(item.Created).local().format(ISO8601DateFormat);
                data.push(medcate);
            }
        }
        let output = new ArrayOutputModel(res.status, data, null);
        output.total = res.json().Total;
        return output;
    }

    private extractDataMultiplePlaces(res: Response) {
        let data: Place[] = [];
        if (res.status == 200) {
            let body = res.json().Places;
            for (let item of body) {
                let medcate = new Place();
                medcate.id = item.Id;
                medcate.city = item.City;
                medcate.country = item.Country
                data.push(medcate);
            }
        }

        let output = new ArrayOutputModel(res.status, data, null);
        output.total = res.json().Total;
        return output;
    }

    private extractDataMultipleDoctor(res: Response) {
        let data: Doctor[] = [];
        if (res.status == 200) {
            let body = res.json().Doctors;
            for (let item of body) {
                let doctor = new Doctor();
                doctor.id = item.Id;
                doctor.firstName = item.FirstName;
                doctor.lastName = item.LastName;
                doctor.fullName = doctor.firstName + ' ' + doctor.lastName;
                doctor.email = item.Email;
                doctor.sex = item.Gender;
                doctor.address = item.Address;
                doctor.phone = item.Phone;
                doctor.role = item.Role;
                doctor.photo = item.Photo;
                doctor.rank = item.Rank;
                doctor.status = item.Status;
                doctor.attachment = item.Profile;
                doctor.dob = moment.utc(item.Birthday).format(ISO8601DateFormat);
                doctor.specialty.id = item.Specialty.Id;
                doctor.specialty.name = item.Specialty.Name;
                doctor.place.id = item.Place.Id;
                doctor.place.city = item.Place.City;
                doctor.place.country = item.Place.Country;
                doctor.noOfVoters = item.Voters;
                doctor.created = item.Created;
                doctor.lastModified = item.LastModified;

                data.push(doctor);
            }
        }

        let output = new ArrayOutputModel(res.status, data, null);
        output.total = res.json().Total;
        return output;
    }

    private extractDataSingleDoctor(res: Response) {
        let data: Doctor;
        if (res.status == 200) {
            data = new Doctor();
            let body = res.json().Doctor;
            data.id = body.Id;
            data.firstName = body.FirstName;
            data.lastName = body.LastName;
            data.fullName = data.firstName + " " + data.lastName;
            data.email = body.Email;
            data.sex = body.Gender;
            data.address = body.Address;
            data.phone = body.Phone;
            data.role = body.Role;
            data.photo = body.Photo;
            data.rank = body.Rank;
            data.status = body.Status;
            data.attachment = body.Profile;
            data.dob = moment.utc(body.Birthday).format(ISO8601DateFormat);
            data.specialty.id = body.Specialty.Id;
            data.specialty.name = body.Specialty.Name;
            data.place.id = body.Place.Id;
            data.place.city = body.Place.City;
            data.place.country = body.Place.Country;
            data.noOfVoters = body.Voters;
            data.created = body.Created;
            data.lastModified = body.LastModified;
        }

        return new SingleOutputModel(res.status, data, null);
    }

    private extractDataStatusStatistic(res: Response) {
        let statusStatistic: StatusStatistic = new StatusStatistic();
        if (res.status == 200) {
            let data = res.json();
            statusStatistic.activeAccounts = data.ActiveAccounts;
            statusStatistic.pendingAccounts = data.PendingAccounts;
            statusStatistic.deactiveAccounts = data.DeactiveAccounts;
            statusStatistic.totalAccounts = data.Total;

        }
        return new SingleOutputModel(res.status, statusStatistic, null);
    }

    private extractDataSinglePatient(res: Response) {
        let data: Patient;
        if (res.status == 200) {
            data = new Patient();
            let body = res.json().Patient;
            data.id = body.Id;
            data.firstName = body.FirstName;
            data.lastName = body.LastName;
            data.fullName = data.firstName + ' ' + data.lastName;
            data.email = body.Email;
            data.sex = body.Gender;
            data.address = body.Address;
            data.role = body.Role;
            data.photo = body.Photo;
            if (body.Birthday) {
                data.dob = moment.utc(body.Birthday).local().format(ISO8601DateFormat);
            }
            data.height = body.Height;
            data.weight = body.Weight;
            data.status = body.Status;
            data.phone = body.Phone;
        }

        return new SingleOutputModel(res.status, data, null);
    }

    private extractDataMultiplePatient(res: Response) {
        let data: Patient[] = [];
        if (res.status == 200) {
            let body = res.json().Patients;
            for (let item of body) {
                let patient = new Patient();
                patient.id = item.Id;
                patient.firstName = item.FirstName;
                patient.lastName = item.LastName;
                patient.fullName = patient.firstName + ' ' + patient.lastName;
                patient.email = item.Email;
                patient.sex = item.Gender;
                patient.address = item.Address;
                patient.role = item.Role;
                patient.photo = item.Photo;
                if (item.Birthday) {
                    patient.dob = moment.utc(item.Birthday).local().format(ISO8601DateFormat);
                }
                patient.height = item.Height;
                patient.weight = item.Weight;
                patient.status = item.Status;
                patient.phone = item.Phone;

                data.push(patient);
            }
        }

        let output = new ArrayOutputModel(res.status, data, null);
        output.total = res.json().Total;
        return output;
    }

    private extractDataChangeStatus(res: Response) {
        if (res.status = 200) {
            return new SingleOutputModel(res.status, true, null);
        } else {
            return new SingleOutputModel(res.status, false, null);
        }
    }

    private handlerError(error: any) {
        let errMsg: string;
        if (error.message) {
            errMsg = error.message;
        } else if (error.status) {
            errMsg = `${error.status} - ${error.statusText} - ${JSON.stringify(error.json())}`;
        } else {
            errMsg = 'Server error';
        }

        console.log(errMsg);
        return Observable.throw(error);
    }
}





