import { Component, ViewEncapsulation, OnInit } from '@angular/core';
import { ROUTER_DIRECTIVES, Router, ActivatedRoute } from "@angular/router";
import {REACTIVE_FORM_DIRECTIVES, FormBuilder, FormGroup, FormControl} from "@angular/forms";
import { Subscription } from 'rxjs/Subscription';

import { Account } from '../../account/account.model';
import { Doctor, StatusStatistic, AccountRole, AccountStatus, WorkingExperient, WEImage } from '../admin.model';
import { AdminService } from '../admin.service';

import { DOMHelper } from '../../shared/helpers';

@Component({
    selector: 'request',
    template: require('./request.component.html'),
    styles: [
        require('../../../sass/pages/request.scss')
    ],
    directives: [ROUTER_DIRECTIVES],
    providers: [FormBuilder]
})
export class RequestComponent implements OnInit {
    private account: Account;

    private recordsPerPage: number = 20;
    private currentPage: number = 0;
    private totalPages: number = 0;
    private doctors: Array<Doctor> = [];
    private statusStat: StatusStatistic = new StatusStatistic();

    private tableDataLoading: boolean = false;
    private statsDataLoading = false;
    private gName: string = '';

    private searchForm: FormGroup;

    constructor(private adminService: AdminService,
        private router: Router,
        private route: ActivatedRoute,
        private builder: FormBuilder) { }

    ngOnInit() {
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (!this.account) {
            this.router.navigate(['/signin']);
        } else {
            if (this.route.snapshot.params['name']) {
                this.gName = this.route.snapshot.params['name'];
            }
            if (this.route.snapshot.params['page']) {
                this.currentPage = +this.route.snapshot.params['page'];
            }
            if (!this.currentPage) this.currentPage = 1;

            this.getPendingDoctors();
        }
        this.setupForm();
    }

    private setupForm() {
        this.searchForm = this.builder.group({
            name: new FormControl(this.gName)
        });
    }

    private getPendingDoctors(pageNo?: number) {
        let getPendingSub: Subscription;
        let getPendingTO = setTimeout(() => {
            if (this.tableDataLoading && getPendingSub && !getPendingSub.isUnsubscribed) {
                getPendingSub.unsubscribe();
                this.tableDataLoading = false;
                this.showFlashError();
            }
        }, 5000);

        let pageNoReq = pageNo || this.currentPage;
        this.tableDataLoading = true;
        if (this.currentPage > this.totalPages) {
            this.currentPage = 1;
        }
        getPendingSub = this.adminService.filterDoctors(this.account, this.gName, null, AccountStatus.PENDING, pageNoReq, this.recordsPerPage).subscribe(
            success => {
                this.tableDataLoading = false;
                clearTimeout(getPendingTO);
                if (success.status == 200) {
                    this.doctors = success.data;
                    this.totalPages = Math.ceil(parseInt(success.total) / this.recordsPerPage);

                    this.currentPage = pageNoReq;
                    let obj = {};
                    if (this.gName) obj['name'] = this.gName;
                    if (this.currentPage > 1) {
                        obj['page'] = this.currentPage;
                    }
                    this.router.navigate(['/admin/requests', obj]);
                } else {
                    console.log("Error in suc" + success.errorMsg);
                }

            },
            error => {
                clearTimeout(getPendingTO);
                this.tableDataLoading = false;
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
        );

        return false;
    }

    searchRecords(data: any, valid: boolean) {
        this.currentPage = 1;
        this.doctors = [];
        let obj = {};
        if (data.name) {
            if (data.name != '')
                obj['name'] = data.name;
            this.gName = data.name;
        } else {
            this.gName = '';
        }
        this.router.navigate(['/admin/requests', obj]);
        this.getPendingDoctors();
    }

    getAllPendingDoctors() {
        this.doctors = [];
        this.searchForm.controls['name'].setValue('');
        this.gName = '';
        this.currentPage = 1;
        this.router.navigate(['/admin/requests']);
        this.getPendingDoctors();
    }

    changeDoctorStatus(doctor: Doctor, status: number) {
        let dropdownSubmenus = document.getElementsByClassName('status-item-dropdown');
        for (let i = 0; i < dropdownSubmenus.length; i++) {
            let htmlElem = <HTMLElement>dropdownSubmenus[i];
            htmlElem.style.display = "none";
            htmlElem.setAttribute('data-isopened', '0');
        }

        let statsSub: Subscription;
        let statsTO = setTimeout(() => {
            if (statsSub && !statsSub.isUnsubscribed) {
                statsSub.unsubscribe();
                this.showFlashError();
            }
        }, 5000);

        let btnStatus = document.getElementById(`btn-status-${doctor.id}`);
        btnStatus.style.opacity = '0.5';
        btnStatus.style.pointerEvents = 'none';

        statsSub = this.adminService.changeStatus(this.account, doctor.id, status)
            .subscribe(
            success => {
                btnStatus.style.opacity = '1';
                btnStatus.style.pointerEvents = 'auto';
                clearTimeout(statsTO);
                if (success.status == 200) {
                    doctor.status = status;
                } else {
                    console.log("E i s" + success.errorMsg);
                }
            },
            error => {
                btnStatus.style.opacity = '1';
                btnStatus.style.pointerEvents = 'auto';
                clearTimeout(statsTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
            );

    }

    // Dropdown
    windowClickDropdownHandler(event: any) {
        let targetElem = <HTMLElement>event.target;
        if (!targetElem.classList.contains('dropdown-item')
            && !targetElem.classList.contains('btn-status')) {
            let dropdownSubmenus = document.getElementsByClassName('status-item-dropdown');
            for (let i = 0; i < dropdownSubmenus.length; i++) {
                let htmlElem = <HTMLElement>dropdownSubmenus[i];
                htmlElem.style.display = "none";
                htmlElem.setAttribute('data-isopened', '0');
            }
        }
    }

    openChangeStatus(id: string) {
        let diarySubmenu = document.getElementById(`status-item-${id}`);
        if (diarySubmenu) {
            let accIsOpened = diarySubmenu.getAttribute('data-isopened');
            if (accIsOpened && accIsOpened == '0') {
                let dropdownSubmenus = document.getElementsByClassName('status-item-dropdown');
                for (let i = 0; i < dropdownSubmenus.length; i++) {
                    let htmlElem = <HTMLElement>dropdownSubmenus[i];
                    htmlElem.style.display = "none";
                    htmlElem.setAttribute('data-isopened', '0');
                }
                diarySubmenu.style.display = "block";
                diarySubmenu.setAttribute('data-isopened', '1');
                window.addEventListener('click', this.windowClickDropdownHandler);
            } else {
                diarySubmenu.style.display = "none";
                diarySubmenu.setAttribute('data-isopened', '0');
                window.removeEventListener('click', this.windowClickDropdownHandler);
            }
        }
    }


    trackByID(index: number, item: Doctor) { return item.id; }

    showFlashError() {
        let item = document.getElementById('flash-noti-error');
        item.style.display = 'block';
        setTimeout(() => {
            item.style.display = 'none';
        }, 4000);
    }
} 



