	module.exports = "<article id=\"request-details-container\" class=\"card\">\r\n\t<header>\r\n\t\t<h3>Pending doctor details</h3>\r\n\t</header>\r\n\t<div id=\"request-details-content\">\r\n\t\t<div id=\"request-details-acc-main\">\r\n\t\t\t<img [attr.src]=\"doctor.photo\" [attr.alt]=\"doctor.fullName\">\r\n\t\t\t<div id=\"acc-details\">\r\n\t\t\t\t<div id=\"acc-details-name\">\r\n\t\t\t\t\t<h4>{{doctor.fullName}}</h4>\r\n\t\t\t\t\t<div id=\"btn-status-wrapper\">\r\n\t\t\t\t\t\t<button class=\"btn-status\" [class.btn-inactive]=\"updatingStatus\" [class.btn-small-active]=\"doctor.status == 2\" [class.btn-small-error]=\"doctor.status == 0\"\r\n\t\t\t\t\t\t\t[class.btn-small-warning]=\"doctor.status == 1\" (click)=\"openChangeStatus(doctor)\"> {{doctor.status == 0 ? 'Disabled' : doctor.status == 1 ? 'Pending' : 'Active'}}</button>\r\n\t\t\t\t\t\t<div class=\"dropdown-content status-item-dropdown\" id=\"pending-dd-status\" data-isopened=\"0\" [attr.data-index]=\"doctor.id\">\r\n\t\t\t\t\t\t\t<div id=\"dropdown-acc-submenu\">\r\n\t\t\t\t\t\t\t\t<a class=\"dropdown-item\" (click)=\"changeDoctorStatus(2)\">Active</a>\r\n\t\t\t\t\t\t\t\t<a class=\"dropdown-item\" (click)=\"changeDoctorStatus(0)\">Disabled</a>\r\n\t\t\t\t\t\t\t</div>\r\n\t\t\t\t\t\t</div>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<button class=\"btn-small-normal btn-download\" ><a id=\"link-download\" target=\"_blank\">Download</a></button>\r\n\t\t\t\t</div>\r\n\t\t\t\t<div id=\"acc-extra-info\" *ngIf=\"doctor.rank\">\r\n\t\t\t\t\t<div class=\"doctor-ranking\">\r\n\t\t\t\t\t\t<ul class=\"ranking-stars\">\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank <= 0\"><i class=\"fa fa-star-o\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank > 0 && doctor.rank < 0.5\"><i class=\"fa fa-star-half-o\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank >= 0.5\"><i class=\"fa fa-star\" aria-hidden=\"true\"></i></li>\r\n\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank <= 1\"><i class=\"fa fa-star-o\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank > 1 && doctor.rank < 1.5\"><i class=\"fa fa-star-half-o\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank >= 1.5\"><i class=\"fa fa-star\" aria-hidden=\"true\"></i></li>\r\n\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank <= 2\"><i class=\"fa fa-star-o\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank > 2 && doctor.rank < 2.5\"><i class=\"fa fa-star-half-o\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank >= 2.5\"><i class=\"fa fa-star\" aria-hidden=\"true\"></i></li>\r\n\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank <= 3\"><i class=\"fa fa-star-o\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank > 3 && doctor.rank < 3.5\"><i class=\"fa fa-star-half-o\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank >= 3.5\"><i class=\"fa fa-star\" aria-hidden=\"true\"></i></li>\r\n\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank < 4\"><i class=\"fa fa-star-o\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank >= 4 && doctor.rank < 4.5\"><i class=\"fa fa-star-half-o\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t\t<li *ngIf=\"doctor.rank >= 4.5\"><i class=\"fa fa-star\" aria-hidden=\"true\"></i></li>\r\n\t\t\t\t\t\t</ul>\r\n\t\t\t\t\t\t<span class=\"ranking-voters\">{{doctor.noOfVoters}}</span>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t</div>\r\n\t\t\t\t<a [attr.href]=\"'mailto:'+doctor.email\" id=\"item-email\">{{doctor.email}}</a>\r\n\t\t\t\t<span id=\"item-specialty\">{{doctor.specialty.name}}</span>\r\n\t\t\t\t<div id=\"acc-info\" *ngIf=\"!profileLoader\">\r\n\t\t\t\t\t<div id=\"acc-address-item\" *ngIf=\"doctor.address != '' \">\r\n\t\t\t\t\t\t<span class=\"exp-item\">Address </span>{{doctor.address}}\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div id=\"acc-city-item\" *ngIf=\"doctor.place.city && doctor.place.country\">\r\n\t\t\t\t\t\t<span class=\"exp-item\">Place: </span>{{doctor.place.city}}, {{doctor.place.country}}\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div id=\"acc-sex-item\" *ngIf=\"doctor.gender\">\r\n\t\t\t\t\t\t<span class=\"exp-item\">Sex </span>{{doctor.gender == 0 ? 'Male' : 'Female'}}\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div id=\"acc-dob-item\" *ngIf=\"doctor.dob\">\r\n\t\t\t\t\t\t<span class=\"exp-item\">Date of birth </span>{{doctor.dob}}\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div id=\"acc-phone-item\" *ngIf=\"doctor.phone\">\r\n\t\t\t\t\t\t<span><span class=\"exp-item\">Phone number </span>{{doctor.phone}}</span>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t</div>\r\n\t\t\t</div>\r\n\t\t</div>\r\n\t</div>\r\n</article>\r\n\r\n<div class=\"imgviewer\" id=\"imgviewer-desc\" data-isopened=\"0\">\r\n\t<div class=\"imgviewer-content\" id=\"imgviewer-desc-content\">\r\n\t\t<img id=\"imgviewer-img\" alt=\"\">\r\n\t</div>\r\n\t<button class=\"btn-close-imgviewer close\" (click)=\"closeImageViewer()\">x</button>\r\n\t<!--<div class=\"cover-img\">\r\n\t</div>-->\r\n</div>";

