	module.exports = "<article id=\"acc-container\" class=\"card\">\r\n\t<header>\r\n\t\t<h3>Patient details</h3>\r\n\t</header>\r\n\t<div id=\"acc-info-content\">\r\n\t\t<div class=\"loader-holder\" *ngIf=\"profileLoader\">\r\n\t\t\t<div class=\"showbox\" *ngIf=\"profileLoader\">\r\n\t\t\t\t<div class=\"loader\">\r\n\t\t\t\t\t<svg class=\"circular\" viewBox=\"25 25 50 50\">\r\n\t\t\t\t\t\t<circle class=\"path\" cx=\"50\" cy=\"50\" r=\"20\" fill=\"none\" stroke-width=\"2\" stroke-miterlimit=\"10\" />\r\n\t\t\t\t\t</svg>\r\n\t\t\t\t</div>\r\n\t\t\t</div>\r\n\t\t</div>\r\n\t\t<div id=\"acc-main\" *ngIf=\"!profileLoader\">\r\n\t\t\t<img [attr.src]=\"patient.photo\" [attr.alt]=\"patient.firstName + '' + patient.lastName\">\r\n\t\t\t<div id=\"acc-details\">\r\n\t\t\t\t<div id=\"acc-details-name\">\r\n\t\t\t\t\t<h4>{{patient.fullName}}</h4>\r\n\t\t\t\t\t<button [class.btn-inactive]=\"updatingStatus\" [class.btn-small-active]=\"patient.status == 2\" [class.btn-small-error]=\"patient.status == 0\" (click)=\"changePatientStatus(patient)\"> {{patient.status == 0 ? 'Disabled' : 'Active'}}</button>\r\n\t\t\t\t</div>\r\n\t\t\t\t<a [attr.href]=\"'mailto:'+patient.email\" id=\"item-email\">{{patient.email}}</a>\r\n\t\t\t\t<div id=\"acc-info\" *ngIf=\"!profileLoader\">\r\n\t\t\t\t\t<div id=\"acc-weight-item\" *ngIf=\"patient.weight\">\r\n\t\t\t\t\t\t<span class=\"exp-item\">Weight </span>{{patient.weight}} <span> kg</span>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div id=\"acc-height-item\" *ngIf=\"patient.height\">\r\n\t\t\t\t\t\t<span class=\"exp-item\">Height </span>{{patient.height}} <span> cm</span>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div id=\"acc-address-item\" *ngIf=\"patient.address\">\r\n\t\t\t\t\t\t<span class=\"exp-item\">address </span>{{patient.address}}\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div id=\"acc-sex-item\" *ngIf=\"patient.gender && patient.gender\">\r\n\t\t\t\t\t\t<span class=\"exp-item\">sex </span>{{patient.gender == 0 ? 'Male' : 'Female'}}\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div id=\"acc-dob-item\" *ngIf=\"patient.dob && patient.dob\">\r\n\t\t\t\t\t\t<span class=\"exp-item\">date of birth </span>{{patient.dob}}\r\n\t\t\t\t\t</div>\r\n\t\t\t\t\t<div id=\"acc-phone-item\" *ngIf=\"patient.phone && patient.phone\">\r\n\t\t\t\t\t\t<span><span class=\"exp-item\">phone number </span>{{patient.phone}}</span>\r\n\t\t\t\t\t</div>\r\n\t\t\t\t</div>\r\n\t\t\t</div>\r\n\t\t</div>\r\n\t</div>\r\n</article>";

