import { Routes, RouterModule }   from '@angular/router';

import { AppComponent } from "./app.component";
import { SigninComponent } from "./account/signin.component";
import { AdminComponent } from "./admin/admin.component";
import { RequestComponent } from "./admin/request/request.component";
import { RequestDetailsComponent } from "./admin/request/request-details.component";
import { PlaceMgtComponent } from "./admin/place/place.component";
import { CategoryMgtComponent } from "./admin/category/category.component";
import { DoctorDetailsComponent } from "./admin/doctor/doctor-details.component";
import { DoctorMgtComponent } from "./admin/doctor/doctormgt.component";
import { PatientDetailsComponent } from "./admin/patient/patient-details.component";
import { PatientMgtComponent } from "./admin/patient/patientmgt.component";
import { PageNotFoundComponent } from "./error/404.component";

import { AuthGuard } from "./shared/auth.guard";

const appRoutes: Routes = [
  {
    path: '',
    redirectTo: '/signin',
    pathMatch: 'full',
  },
  {
    path: 'signin',
    component: SigninComponent
  },
  {
    path: 'admin',
    component: AdminComponent,
    data: {
      title: 'Administrator'
    },
    canActivate: [AuthGuard],
    children: [
      {
        path: '',
        component: RequestComponent,
      },
      {
        path: 'requests',
        component: RequestComponent,
      },
      {
        path: 'requests/:rqid',
        component: RequestDetailsComponent,
      },
      {
        path: 'doctors',
        component: DoctorMgtComponent
      },
      {
        path: 'doctors/:drid',
        component: DoctorDetailsComponent
      },
      {
        path: 'patients',
        component: PatientMgtComponent
      },
      {
        path: 'patients/:ptid',
        component: PatientDetailsComponent
      },
      {
        path: 'places',
        component: PlaceMgtComponent
      },
      {
        path: 'categories',
        component: CategoryMgtComponent
      }
    ]
  },
  { path: '**', component: PageNotFoundComponent },
  { path: '404', component: PageNotFoundComponent }
];

export const appRoutingProviders: any[] = [
  AuthGuard
];

export const routing = RouterModule.forRoot(appRoutes);


