import { NgModule }       from '@angular/core';
import { BrowserModule }  from '@angular/platform-browser';
import { FormsModule }    from '@angular/forms';
import { RouterModule }    from '@angular/router';
import { HTTP_PROVIDERS } from '@angular/http';

import { AppComponent }       from './app.component';
import { routing, appRoutingProviders } from './app.routing';

import { SigninComponent } from "./account/signin.component";
import { AdminComponent } from "./admin/admin.component";
import { RequestComponent } from "./admin/request/request.component";
import { RequestDetailsComponent } from "./admin/request/request-details.component";
import { PlaceMgtComponent } from "./admin/place/place.component";
import { CategoryMgtComponent } from "./admin/category/category.component";
import { DoctorDetailsComponent } from "./admin/doctor/doctor-details.component";
import { DoctorMgtComponent } from "./admin/doctor/doctormgt.component";
import { PatientDetailsComponent } from "./admin/patient/patient-details.component";
import { PatientMgtComponent } from "./admin/patient/patientmgt.component";
import { PageNotFoundComponent } from "./error/404.component";

import { AccountService } from "./account/account.service";
import { AdminService } from "./admin/admin.service";

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    RouterModule,
    routing
  ],
  declarations: [
    AppComponent,
    SigninComponent,
    AdminComponent,
    RequestComponent,
    RequestDetailsComponent,
    PlaceMgtComponent,
    CategoryMgtComponent,
    DoctorDetailsComponent,
    DoctorMgtComponent,
    PatientDetailsComponent,
    PatientMgtComponent,
    PageNotFoundComponent
  ],
  providers: [
    appRoutingProviders,
    AccountService,
    AdminService,
    HTTP_PROVIDERS
  ],
  bootstrap: [ AppComponent ]
})
export class AppModule {
}


