import { Component, ViewEncapsulation, OnInit } from '@angular/core';
import { ROUTER_DIRECTIVES, Router, ActivatedRoute } from "@angular/router";
import {REACTIVE_FORM_DIRECTIVES, FormBuilder, FormGroup, FormControl} from "@angular/forms";
import {Validators} from "@angular/common";
import { Subscription } from 'rxjs/Subscription';
import * as lodash from "lodash";
import * as moment from "moment";

import { Account } from '../../account/account.model';
import { Place } from '../admin.model';
import { AdminService } from '../admin.service';

import { DOMHelper } from '../../shared/helpers';

@Component({
    selector: 'placemgt',
    template: require('./place.component.html'),
    styles: [
        require('../../../sass/pages/place.scss')
    ],
    directives: [ROUTER_DIRECTIVES],
    providers: [FormBuilder]
})
export class PlaceMgtComponent implements OnInit {
    private account: Account;

    private recordsPerPage: number = 20;
    private currentPage: number = 1;
    private totalPages: number;
    private totalRecords: number = 0;
    private places: Place[] = [];

    private showLoader = false;

    private searchForm: FormGroup;
    private addPlaceForm: FormGroup;
    private editPlaceForm: FormGroup;
    private submitAddAttempt = false;
    private submitEditAttempt = false;
    private searchingPlaces = false;
    private addingPlace = false;
    private editingPlace = false;
    private deletingPlace = false;
    private existPlace = false;

    private countries: string[] = [];
    private selectedCountry: string = '';
    private gCountry = '';
    private gCity = '';
    private countriesBloodhound: any;
    private chosenPlace: Place = new Place();

    constructor(private adminService: AdminService,
        private router: Router, private route: ActivatedRoute,
        private builder: FormBuilder) { }

    ngOnInit() {
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (!this.account) {
            this.router.navigate(['/signin']);
        } else {
            if (this.route.snapshot.params['city']) {
                this.gCity = this.route.snapshot.params['city'];
            }
            if (this.route.snapshot.params['country']) {
                this.gCountry = this.route.snapshot.params['country'];
            }
            if (this.route.snapshot.params['page']) {
                this.currentPage = +this.route.snapshot.params['page'];
            }
            if (!this.currentPage) this.currentPage = 1;

            this.getNUmberOfPlaces();
            this.getPlaces();
        }
        this.formSetup();
    }

    private initTypeaheadAdd(name: string) {
        $('#the-basics-add #u_acountry').typeahead({
            hint: true,
            highlight: true,
            minLength: 1
        },
            {
                name: name,
                source: this.countriesBloodhound,
                templates: {
                    suggestion: function (data: string) {
                        return `<span style="display: block; padding: 4px 10px">${data}</span>`;
                    }
                }
            }).on('typeahead:selected', (obj: any, datum: any) => {
                this.addPlaceForm.controls['country'].setValue(datum);
            });;
    }

    private initTypeaheadEdit(name: string) {
        $('#u_country').typeahead({
            hint: true,
            highlight: true,
            minLength: 1
        },
            {
                name: name,
                source: this.countriesBloodhound,
                templates: {
                    suggestion: function (data: string) {
                        return `<span style="display: block; padding: 4px 10px">${data}</span>`;
                    }
                }
            }).on('typeahead:selected', (obj: any, datum: any) => {
                this.editPlaceForm.controls['country'].setValue(datum);
            });;
    }

    private formSetup() {
        this.searchForm = this.builder.group({
            city: new FormControl(this.gCity),
            country: new FormControl(this.gCountry),
        });
        this.addPlaceForm = this.builder.group({
            city: new FormControl('', <any>Validators.compose([<any>Validators.required, <any>Validators.maxLength(64)])),
            country: new FormControl('', <any>Validators.compose([<any>Validators.required, <any>Validators.maxLength(64)])),
        });
        this.editPlaceForm = this.builder.group({
            id: new FormControl('', null, null),
            city: new FormControl('', <any>Validators.compose([<any>Validators.required, <any>Validators.maxLength(64)])),
            country: new FormControl('', <any>Validators.compose([<any>Validators.required, <any>Validators.maxLength(64)])),
        });
    }

    private getNUmberOfPlaces() {
        let getPlacesSub: Subscription;
        let getPlacesTO = setTimeout(() => {
            if (getPlacesSub && !getPlacesSub.isUnsubscribed) {
                getPlacesSub.unsubscribe();
                this.showFlashError();
            }
        }, 5000);

        getPlacesSub = this.adminService.filterPlaces(this.account, null, null, null, null)
            .subscribe(
            success => {
                clearTimeout(getPlacesTO);
                if (success.status == 200) {
                    this.totalRecords = success.total;
                    let pls: Place[] = success.data;
                    for (let item of pls) {
                        if (!this.countries.includes(item.country)) {
                            this.countries.push(item.country);
                        }
                    }
                    this.countriesBloodhound = new Bloodhound({
                        datumTokenizer: Bloodhound.tokenizers.whitespace,
                        queryTokenizer: Bloodhound.tokenizers.whitespace,
                        local: this.countries
                    });
                } else {
                    console.log("Error in suc" + success.errorMsg);
                }

            },
            error => {
                clearTimeout(getPlacesTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
            );
    }
    private getPlaces(pageNo?: number) {
        let getPlacesSub: Subscription;
        let getPlacesTO = setTimeout(() => {
            if (this.showLoader && getPlacesSub && !getPlacesSub.isUnsubscribed) {
                getPlacesSub.unsubscribe();
                this.showLoader = false;
                this.showFlashError();
            }
        }, 5000);

        let pageNoReq = pageNo || this.currentPage;

        this.showLoader = true;
        getPlacesSub = this.adminService.filterPlaces(this.account, this.gCity, this.gCountry, pageNoReq, this.recordsPerPage)
            .subscribe(
            success => {
                clearTimeout(getPlacesTO);
                this.showLoader = false;
                this.searchingPlaces = false;
                if (success.status == 200) {
                    this.currentPage = pageNoReq;
                    this.places = success.data;
                    this.totalPages = Math.ceil(parseInt(success.total) / this.recordsPerPage);

                    let obj = {};
                    if (this.gCountry) {
                        obj['country'] = this.gCountry;
                    }
                    if (this.gCity) {
                        obj['city'] = this.gCity;
                    }
                    if (this.currentPage > 1) {
                        obj['page'] = this.currentPage;
                    }
                    this.router.navigate(['/admin/places', obj]);
                } else {
                    console.log("Error in suc" + success.errorMsg);
                }

            },
            error => {
                clearTimeout(getPlacesTO);
                this.showLoader = false;
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
            );
    }

    private refreshPlaces(pageNo?: number) {
        let refPlacesSub: Subscription;
        let refPlacesTO = setTimeout(() => {
            if (this.showLoader && refPlacesSub && !refPlacesSub.isUnsubscribed) {
                refPlacesSub.unsubscribe();
                this.showLoader = false;
                this.showFlashError();
            }
        }, 5000);

        let pageNoReq = pageNo || this.currentPage;
        refPlacesSub = this.adminService.filterPlaces(this.account, this.gCity, this.gCountry, pageNoReq, this.recordsPerPage)
            .subscribe(
            success => {
                clearTimeout(refPlacesTO);
                if (success.status == 200) {
                    this.currentPage = pageNoReq;
                    this.places = success.data;
                    this.totalPages = Math.ceil(parseInt(success.total) / this.recordsPerPage);
                    this.getNUmberOfPlaces();
                } else {
                    console.log("Error in suc" + success.errorMsg);
                }
            },
            error => {
                clearTimeout(refPlacesTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
            );
    }

    getAllPlaces() {
        this.gCity = '';
        this.gCountry = '';
        this.currentPage = 1;
        this.searchForm.controls['country'].setValue('');
        this.searchForm.controls['city'].setValue('');
        this.router.navigate(['/admin/places']);
        this.getPlaces();
    }

    private searchPlaces(data: any) {
        this.showLoader = true;
        this.searchingPlaces = true;
        this.currentPage = 1;

        let obj = {};
        if (data.country) {
            obj['country'] = data.country;
            this.gCountry = data.country;
        } else {
            this.gCountry = '';
        }
        if (data.city) {
            obj['city'] = data.city;
            this.gCity = data.city;
        } else {
            this.gCity = '';
        }
        if (this.currentPage > 1) {
            obj['page'] = this.currentPage;
        }
        this.router.navigate(['/admin/places', obj]);
        this.getPlaces();
    }

    createPlace(data: any, isValid: boolean) {

        this.submitAddAttempt = true;
        if (isValid) {
            let createPlacesSub: Subscription;
            let createPlacesTO = setTimeout(() => {
                if (this.addingPlace && createPlacesSub && !createPlacesSub.isUnsubscribed) {
                    createPlacesSub.unsubscribe();
                    this.addingPlace = false;
                    this.showFlashError();
                }
            }, 5000);
            this.addingPlace = true;
            let place = new Place();
            place.city = data.city;
            place.country = data.country;
            createPlacesSub = this.adminService.createPlace(this.account, place)
                .subscribe(
                success => {
                    this.addingPlace = false;
                    clearTimeout(createPlacesTO);
                    if (success.status == 200) {
                        this.refreshPlaces();
                    } else {
                        console.log("eis" + success.data);
                    }
                    this.closeAddPlaceModel();
                },
                error => {
                    clearTimeout(createPlacesTO);
                    this.addingPlace = false;
                    if (!error.status) {
                        this.showFlashError();
                        this.closeAddPlaceModel();
                    } else {
                        if (error.status == 409) {
                            this.existPlace = true;
                            this.submitAddAttempt = false;
                        } else {
                            console.log("Error" + error);
                            this.closeAddPlaceModel();
                        }
                    }
                }
                );
        }
    }

    editPlace(data: any, isValid: boolean) {
        this.submitEditAttempt = true;
        if (data.city == this.chosenPlace.city && data.country == this.chosenPlace.country) {
            this.submitEditAttempt = false;
            this.closeEditPlaceModel();
            return false;
        }
        if (isValid) {
            let createPlacesSub: Subscription;
            let createPlacesTO = setTimeout(() => {
                if (this.editingPlace && createPlacesSub && !createPlacesSub.isUnsubscribed) {
                    createPlacesSub.unsubscribe();
                    this.editingPlace = false;
                    this.showFlashError();
                }
            }, 5000);

            this.editingPlace = true;
            let place = new Place();
            place.id = data.id;
            place.city = data.city;
            place.country = data.country;
            createPlacesSub = this.adminService.updatePlace(this.account, place)
                .subscribe(
                success => {
                    this.editingPlace = false;
                    clearTimeout(createPlacesTO);
                    if (success.status == 200) {
                        this.refreshPlaces();
                    } else {
                        console.log("eis" + success.data);
                    }
                    this.closeEditPlaceModel();
                },
                error => {
                    this.editingPlace = false;
                    clearTimeout(createPlacesTO);
                    if (!error.status) {
                        this.showFlashError();
                        this.closeEditPlaceModel();
                    } else {
                        if (error.status == 409) {
                            this.existPlace = true;
                            this.submitEditAttempt = false;
                        } else {
                            console.log("Error" + error);
                            this.closeEditPlaceModel();
                        }
                    }
                }
                );
        }
    }

    deletePlace(place: Place) {

    }
    // Modal
    windowClickModalHandler(event: any) {
        if (event.target.id == 'add-place-modal') {
            let placeModal = document.getElementById('add-place-modal');
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            placeModal.style.display = 'none';
            placeModal.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickModalHandler);
        }
        if (event.target.id == 'edit-place-modal') {
            let placeModal = document.getElementById('edit-place-modal');
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            placeModal.style.display = 'none';
            placeModal.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickModalHandler);
        }
    }

    openAddNewPlace() {
        this.submitAddAttempt = false;
        this.addPlaceForm.controls['city'].setValue('');
        this.addPlaceForm.controls['city'].markAsPristine();
        this.addPlaceForm.controls['country'].setValue('');
        this.addPlaceForm.controls['country'].markAsPristine();
        let placeModel = document.getElementById('add-place-modal');
        if (placeModel.getAttribute('data-isopened') == '0') {
            this.initTypeaheadAdd(moment().utc().valueOf() + '');
            document.getElementsByTagName('body')[0].style.overflow = 'hidden';
            placeModel.style.display = 'flex';
            placeModel.setAttribute('data-isopened', '1');
            window.addEventListener('click', this.windowClickModalHandler);
        }
    }

    openEditPlace(place: Place) {
        this.submitEditAttempt = false;
        this.editPlaceForm.controls['id'].setValue(place.id);
        this.editPlaceForm.controls['city'].setValue(place.city);
        this.editPlaceForm.controls['country'].setValue(place.country);
        this.chosenPlace = place;

        let placeModel = document.getElementById('edit-place-modal');
        if (placeModel.getAttribute('data-isopened') == '0') {
            this.initTypeaheadEdit(moment().utc().valueOf() + '');
            document.getElementsByTagName('body')[0].style.overflow = 'hidden';
            placeModel.style.display = 'flex';
            placeModel.setAttribute('data-isopened', '1');
            window.addEventListener('click', this.windowClickModalHandler);
        }
    }

    closeAddPlaceModel() {
        let placeModel = document.getElementById('add-place-modal');
        if (placeModel.getAttribute('data-isopened') == '1') {
            $('#u_acountry').typeahead('destroy')
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            placeModel.style.display = 'none';
            placeModel.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickModalHandler);
        }
    }

    closeEditPlaceModel() {
        let placeModel = document.getElementById('edit-place-modal');
        if (placeModel.getAttribute('data-isopened') == '1') {
            $('#u_country').typeahead('destroy')
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            placeModel.style.display = 'none';
            placeModel.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickModalHandler);
        }
    }
    // End Modal
    trackByID(index: number, item: Place) { return item.id; }

    showFlashError() {
        let item = document.getElementById('flash-noti-error');
        item.style.display = 'block';
        setTimeout(() => {
            item.style.display = 'none';
        }, 4000);
    }

}



