import {Component, OnInit, OnDestroy} from "@angular/core";
import {Router, ActivatedRoute} from "@angular/router";
import { Subscription } from 'rxjs/Subscription';

import { Account } from '../../account/account.model';
import { Patient, AccountRole, AccountStatus } from '../admin.model';
import { AdminService } from '../admin.service';
import { DOMHelper } from "../../shared/helpers";

@Component({
    selector: 'admin-patient-details',
    template: require('./patient-details.component.html'),
    styles: [
        require('../../../sass/pages/patient-details.scss')
    ],
})
export class PatientDetailsComponent implements OnInit, OnDestroy {
    private account: Account;

    private patient: Patient = new Patient();
    private sub: Subscription;

    private profileLoader = false;
    private updatingStatus = false;

    constructor(private router: Router,
        private route: ActivatedRoute,
        private adminservice: AdminService) { }

    ngOnInit() {
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (!this.account) {
            this.router.navigate(['/signin']);
        } else {
            DOMHelper.scrollUpTo(document.getElementsByTagName('body')[0], 0, 600);

            this.sub = this.route.params.subscribe(params => {
                let id = params['ptid'];
                this.getPatientProfile(id);
            });
        }
    }

    ngOnDestroy() {
        this.sub.unsubscribe();
    }

    private getPatientProfile(id: string) {
        let getPatientSub: Subscription;
        let getPatientTO = setTimeout(() => {
            if (this.profileLoader && getPatientSub && !getPatientSub.isUnsubscribed) {
                getPatientSub.unsubscribe();
                this.profileLoader = false;
                this.showFlashError();
            }
        }, 5000);

        this.profileLoader = true;
        getPatientSub = this.adminservice.getPatient(this.account, id).subscribe(
            success => {
                clearTimeout(getPatientTO);
                this.profileLoader = false;
                if (success.status == 200)
                    this.patient = success.data;
                else
                    console.log("eis" + success.errorMsg);
            },
            error => {
                clearTimeout(getPatientTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    if (error.status = 404) {
                        this.router.navigate(['/404']);
                    } else {
                        console.log("Error" + error);
                    }
                }
                this.profileLoader = false;
            }
        );
    }

    changePatientStatus() {
        let changeStatusSub: Subscription;
        let changeStatusTO = setTimeout(() => {
            console.log(this.profileLoader)
            if (this.profileLoader && changeStatusSub && !changeStatusSub.isUnsubscribed) {
                changeStatusSub.unsubscribe();
                this.profileLoader = false;
                this.showFlashError();
            }
        }, 5000);

        let newStatus = this.patient.status == AccountStatus.DISABLED ? AccountStatus.ACTIVE : AccountStatus.DISABLED;
        this.updatingStatus = true;
        changeStatusSub = this.adminservice.changeStatus(this.account, this.patient.id, newStatus)
            .subscribe(
            success => {
                this.updatingStatus = false;
                if (success.status == 200) {
                    this.patient.status = newStatus;
                } else {
                    console.log("E i s" + success.errorMsg);
                }

                clearTimeout(changeStatusTO);
            },
            error => {
                this.updatingStatus = false;
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
                clearTimeout(changeStatusTO);
            }
            );
    }

    showFlashError() {
        let item = document.getElementById('flash-noti-error');
        item.style.display = 'block';
        setTimeout(() => {
            item.style.display = 'none';
        }, 4000);
    }
}


