import {Component, OnInit, OnDestroy} from "@angular/core";
import {Router, ActivatedRoute} from "@angular/router";
import {REACTIVE_FORM_DIRECTIVES, FormBuilder, FormGroup, FormControl} from "@angular/forms";
import { Subscription } from 'rxjs/Subscription';

import { Account } from '../../account/account.model';
import { Doctor, StatusStatistic, AccountRole, AccountStatus, WorkingExperient, WEImage } from '../admin.model';
import { AdminService } from '../admin.service';

import { DOMHelper } from '../../shared/helpers';

@Component({
    selector: 'admin-doctors',
    template: require('./doctormgt.component.html'),
    styles: [
        require('../../../sass/pages/doctors.scss')
    ],
    providers: [FormBuilder]
})
export class DoctorMgtComponent implements OnInit {
    private account: Account;

    private recordsPerPage: number = 20;
    private currentPage: number = 0;
    private totalPages: number = 0;
    private doctors: Array<Doctor> = [];
    private statusStat: StatusStatistic = new StatusStatistic();

    private tableDataLoading: boolean = false;
    private gName: string = '';
    private gStatus: number = -1;

    private searchForm: FormGroup;

    constructor(private adminService: AdminService,
        private router: Router,
        private route: ActivatedRoute,
        private builder: FormBuilder) { }

    ngOnInit() {
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (!this.account) {
            this.router.navigate(['/signin']);
        } else {
            if (this.route.snapshot.params['name']) {
                this.gName = this.route.snapshot.params['name'];
            }
            if (this.route.snapshot.params['status']) {
                this.gStatus = this.route.snapshot.params['status'];
            }
            if (this.route.snapshot.params['page']) {
                this.currentPage = +this.route.snapshot.params['page'];
            }
            if (!this.currentPage) this.currentPage = 1;

            this.getStatusStatistic();
            this.getDoctors();
        }
        this.setupForm();
    }

    private setupForm() {
        this.searchForm = this.builder.group({
            status: new FormControl(this.gStatus),
            name: new FormControl(this.gName)
        });
    }

    private getDoctors(pageNo?: number) {
        let getDoctorsSub: Subscription;
        let getDoctorsTO = setTimeout(() => {
            if (this.tableDataLoading && getDoctorsSub && !getDoctorsSub.isUnsubscribed) {
                getDoctorsSub.unsubscribe();
                this.tableDataLoading = false;
                this.showFlashError();
            }
        }, 5000);

        let pageNoReq: number = pageNo || this.currentPage;
        this.tableDataLoading = true;
        console.log(`${this.gName} -- ${this.gStatus}`);
        getDoctorsSub = this.adminService.filterDoctors(this.account, this.gName, null, this.gStatus, pageNoReq, this.recordsPerPage).subscribe(
            success => {
                this.tableDataLoading = false;
                clearTimeout(getDoctorsTO);
                if (success.status == 200) {
                    this.doctors = success.data;
                    this.totalPages = Math.ceil(parseInt(success.total) / this.recordsPerPage);
                    this.currentPage = pageNoReq;

                    let obj = { };
                    if (this.gName) obj['name'] = this.gName;
                    if (this.gStatus && this.gStatus != -1) obj['status'] = this.gStatus;
                    if (this.currentPage > 1) {
                        obj['page'] = this.currentPage;
                    }
                    this.router.navigate(['/admin/doctors', obj]);

                } else {
                    console.log("Error in suc" + success.errorMsg);
                }
            },
            error => {
                this.tableDataLoading = false;
                clearTimeout(getDoctorsTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
        );

        return false;
    }

    getStatusStatistic() {
        let getStatsSub: Subscription;
        let getStatsTO = setTimeout(() => {
            if (getStatsSub && !getStatsSub.isUnsubscribed) {
                getStatsSub.unsubscribe();
                this.showFlashError();
            }
        }, 5000);

        getStatsSub = this.adminService.getAccountStatusStatistic(this.account, AccountRole.DOCTOR)
            .subscribe(
            success => {
                clearTimeout(getStatsTO);
                if (success.status == 200)
                    this.statusStat = success.data;
                else {
                    console.log("E i s" + success.errorMsg);
                }

            },
            error => {
                clearTimeout(getStatsTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
            );
    }

    searchRecords(data: any, valid: boolean) {
        this.currentPage = 1;
        this.doctors = [];
        let obj = {};
        if (data.name) {
            if (data.name != '')
                obj['name'] = data.name;
            this.gName = data.name;
        } else {
            this.gName = '';
        }
        if (data.status) {
            if (data.status != -1)
                obj['status'] = data.status;
            this.gStatus = data.status;
        } else {
            this.gStatus = -1;
        }
        this.router.navigate(['/admin/doctors', obj]);
        this.getDoctors();
    }

    getAllDoctors() {
        this.doctors = [];
        this.searchForm.controls['name'].setValue('');
        this.searchForm.controls['status'].setValue(-1);
        this.gName = '';
        this.gStatus = -1;
        this.currentPage = 1;
        this.router.navigate(['/admin/doctors']);
        this.getDoctors();
    }

    changeDoctorStatus(doctor: Doctor) {
        let statsSub: Subscription;
        let statsTO = setTimeout(() => {
            if (statsSub && !statsSub.isUnsubscribed) {
                statsSub.unsubscribe();
                this.showFlashError();
            }
        }, 5000);

        let newStatus = doctor.status == AccountStatus.DISABLED ? AccountStatus.ACTIVE : AccountStatus.DISABLED;
        let btnStatus = document.getElementById(`btn-status-${doctor.id}`);
        btnStatus.style.opacity = '0.5';
        btnStatus.style.pointerEvents = 'none';
        statsSub = this.adminService.changeStatus(this.account, doctor.id, newStatus)
            .subscribe(
            success => {
                if (success.status == 200) {
                    doctor.status = newStatus;
                    this.getStatusStatistic();
                } else {
                    console.log("E i s" + success.errorMsg);
                }

                btnStatus.style.opacity = '1';
                btnStatus.style.pointerEvents = 'auto';
                clearTimeout(statsTO);
            },
            error => {
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }

                btnStatus.style.opacity = '1';
                btnStatus.style.pointerEvents = 'auto';
                clearTimeout(statsTO);
            }
            );

    }

    trackByID(index: number, item: Doctor) { return item.id; }

    showFlashError() {
        let item = document.getElementById('flash-noti-error');
        item.style.display = 'block';
        setTimeout(() => {
            item.style.display = 'none';
        }, 4000);
    }
} 



