import {Component, OnInit} from "@angular/core";
import {REACTIVE_FORM_DIRECTIVES, FormBuilder, FormGroup, FormControl} from "@angular/forms";
import {Validators} from "@angular/common";
import {Router} from "@angular/router";
import { HTTP_PROVIDERS } from '@angular/http';
import { Subscription } from 'rxjs/Subscription';

import messages from "../shared/messages";
import {Account, SigninModel} from "./account.model";
import { AccountService }from "./account.service";

@Component({
    selector: 'login',
    template: require('./signin.component.html'),
    styles: [
        require('../../sass/pages/signin.scss')
    ],
    directives: [REACTIVE_FORM_DIRECTIVES],
    providers: [FormBuilder, HTTP_PROVIDERS]
})
export class SigninComponent {

    private signinForm: FormGroup;
    private submitAttempt = false;

    private signingin: boolean = false;
    private invalidData = false;


    constructor(private router: Router, private accountService: AccountService, private formBuilder: FormBuilder) {
    }

    ngOnInit() {
        if (JSON.parse(window.sessionStorage.getItem('account'))) {
            this.router.navigate(['/admin']);
        }
        this.formSetup();
    }

    private formSetup() {
        this.signinForm = this.formBuilder.group({
            email: new FormControl('', <any>Validators.required),
            password: new FormControl('', <any>Validators.required)
        });
    }

    signIn(signinData: any, isValid: boolean) {
        this.submitAttempt = true;
        if (isValid) {
            let signSub: Subscription;
            let signTO = setTimeout(() => {
                if (this.signingin && signSub && !signSub.isUnsubscribed) {
                    signSub.unsubscribe();
                    this.signingin = false;
                    this.showFlashError();
                }
            }, 5000);


            this.signingin = true;
            let model = new SigninModel();
            model.email = signinData.email;
            model.password = signinData.password;
            signSub = this.accountService.signIn(model)
                .subscribe(
                success => {
                    clearTimeout(signTO);
                    if (success.status == 200) {
                        window.sessionStorage.setItem('account', JSON.stringify(success.data));
                    } else {
                        console.log("eis" + success.data);
                    }
                    this.signingin = false;
                    this.submitAttempt = false;
                    this.router.navigate(['/admin/requests']);
                },
                error => {
                    clearTimeout(signTO);
                    if (!error.status) {
                        this.showFlashError();
                    } else {
                        this.invalidData = true;
                    }
                    this.submitAttempt = false;
                    this.signingin = false;
                }
                );
        }

        return false;
    }

    showFlashError() {
        let item = document.getElementById('flash-noti-error');
        item.style.display = 'block';
        setTimeout(() => {
            item.style.display = 'none';
        }, 4000);
    }
} 



