import { Component, ViewEncapsulation, OnInit } from '@angular/core';
import { ROUTER_DIRECTIVES, Router, ActivatedRoute } from "@angular/router";
import {REACTIVE_FORM_DIRECTIVES, FormBuilder, FormGroup, FormControl} from "@angular/forms";
import {Validators} from "@angular/common";
import { Subscription } from 'rxjs/Subscription';

import { Account } from '../../account/account.model';
import { MedicalCategory } from '../admin.model';
import { AdminService } from '../admin.service';

import { DOMHelper } from '../../shared/helpers';

@Component({
    selector: 'categorymgt',
    template: require('./category.component.html'),
    styles: [
        require('../../../sass/pages/category.scss')
    ],
    directives: [ROUTER_DIRECTIVES],
    providers: [FormBuilder]
})
export class CategoryMgtComponent implements OnInit {
    private account: Account;

    private recordsPerPage: number = 20;
    private currentPage: number = 1;
    private totalPages: number;
    private totalRecords: number;
    private caregories: MedicalCategory[] = [];

    private showLoader = false;

    private newCateForm: FormGroup;
    private editCateForm: FormGroup;
    private submitAddAttempt = false;
    private submitEditAttempt = false;
    private addingCate = false;
    private editingCate = false;
    private existsCate = false;
    private chosenCate = new MedicalCategory();

    private getCategoriesSub: Subscription;

    constructor(private adminService: AdminService,
        private router: Router, private route: ActivatedRoute,
        private builder: FormBuilder) { }

    ngOnInit() {
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (!this.account) {
            this.router.navigate(['/signin']);
        } else {
            if (this.route.snapshot.params['page']) {
                this.currentPage = +this.route.snapshot.params['page'];
            }
            if (!this.currentPage) this.currentPage = 1;
            
            this.getNumberCategories();
            this.getCategories();
        }
        this.formSetup();
    }

    private formSetup() {
        this.newCateForm = this.builder.group({
            name: new FormControl('', <any>Validators.compose([<any>Validators.required, <any>Validators.maxLength(32)]))
        });
        this.editCateForm = this.builder.group({
            id: new FormControl(''),
            name: new FormControl('', <any>Validators.compose([<any>Validators.required, <any>Validators.maxLength(32)]))
        });
    }

    private getNumberCategories(pageNo?: number) {
        let getCategoriesTO = setTimeout(() => {
            if (this.showLoader && this.getCategoriesSub && !this.getCategoriesSub.isUnsubscribed) {
                this.getCategoriesSub.unsubscribe();
                this.showLoader = false;
                this.showFlashError();
            }
        }, 5000);
        this.getCategoriesSub = this.adminService.filterMedrecordCategories(this.account, null, null, null).subscribe(
            success => {
                clearTimeout(getCategoriesTO);
                if (success.status == 200) {
                    this.totalRecords = success.total;
                } else {
                    console.log("Error in suc" + success.errorMsg);
                }

            },
            error => {
                clearTimeout(getCategoriesTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
        );
    }

    private getCategories(pageNo?: number) {
        let getCategoriesTO = setTimeout(() => {
            if (this.showLoader && this.getCategoriesSub && !this.getCategoriesSub.isUnsubscribed) {
                this.getCategoriesSub.unsubscribe();
                this.showLoader = false;
                this.showFlashError();
            }
        }, 5000);

        let pagenoreq = pageNo || this.currentPage;

        this.showLoader = true;
        this.getCategoriesSub = this.adminService.filterMedrecordCategories(this.account, null, pagenoreq, this.recordsPerPage).subscribe(
            success => {
                this.showLoader = false;
                clearTimeout(getCategoriesTO);
                if (success.status == 200) {
                    this.caregories = success.data;
                    this.totalPages = Math.ceil(parseInt(success.total) / this.recordsPerPage);
                    this.currentPage = pagenoreq;

                    let obj = {};
                    if (this.currentPage > 1) {
                        obj['page'] = this.currentPage;
                    }
                    this.router.navigate(['/admin/categories', obj]);

                } else {
                    console.log("Error in suc" + success.errorMsg);
                }

            },
            error => {
                this.showLoader = false;
                clearTimeout(getCategoriesTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
        );
    }

    private refreshCategories(pageNo?: number) {
        let pageNoReq = pageNo || this.currentPage;

        let getCategoriesTO = setTimeout(() => {
            if (this.getCategoriesSub && !this.getCategoriesSub.isUnsubscribed) {
                this.getCategoriesSub.unsubscribe();
                this.showFlashError();
            }
        }, 5000);

        this.getCategoriesSub = this.adminService.filterMedrecordCategories(this.account, null, pageNoReq, this.recordsPerPage).subscribe(
            success => {
                clearTimeout(getCategoriesTO);
                if (success.status == 200) {
                    this.currentPage = pageNoReq;
                    this.caregories = success.data;
                    this.totalPages = Math.ceil(parseInt(success.total) / this.recordsPerPage);

                    let obj = {};
                    if (this.currentPage > 1) {
                        obj['page'] = this.currentPage;
                    }
                    this.router.navigate(['/admin/categories', obj]);
                } else {
                    console.log("Error in suc" + success.errorMsg);
                }

            },
            error => {
                clearTimeout(getCategoriesTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
        );
    }

    createCategory(data: any, isValid: boolean) {
        this.submitAddAttempt = true;
        this.existsCate = false;

        if (isValid) {
            let createCateSub: Subscription;
            let createCateTO = setTimeout(() => {
                if (createCateSub && !createCateSub.isUnsubscribed) {
                    createCateSub.unsubscribe();
                    this.showFlashError();
                    this.submitAddAttempt = false;
                }
            }, 5000);

            let medCategory = new MedicalCategory();
            medCategory.name = data.name;
            this.addingCate = true;
            createCateSub = this.adminService.createMedrecordCategory(this.account, medCategory)
                .subscribe(
                success => {
                    clearTimeout(createCateTO);
                    this.addingCate = false;
                    if (success.status == 200) {
                        this.refreshCategories();
                    } else {
                        console.log("eis" + success.data);
                    }
                    this.closeAddCategoryModel();
                    this.newCateForm.controls['name'].setValue('');
                },
                error => {
                    this.addingCate = false;
                    clearTimeout(createCateTO);
                    if (!error.status) {
                        this.showFlashError();
                        this.closeAddCategoryModel();
                    } else {
                        if (error.status == 409) {
                            this.existsCate = true;
                            this.submitAddAttempt = false;
                        } else {
                            console.log("Error" + error);
                            this.closeAddCategoryModel();
                        }
                    }
                }
                );
        }

    }

    editCategory(data: any, isValid: boolean) {
        this.submitEditAttempt = true;
        this.existsCate = false;

        if (data.name = this.chosenCate.name) {
            this.submitEditAttempt = false;
            this.closeEditCategoryModel();
            return false;
        }
        if (isValid) {
            let editCateSub: Subscription;
            let editCateTO = setTimeout(() => {
                if (editCateSub && !editCateSub.isUnsubscribed) {
                    editCateSub.unsubscribe();
                    this.showFlashError();
                    this.submitAddAttempt = false;
                }
            }, 5000);

            let category = new MedicalCategory();
            category.id = data.id;
            category.name = data.name;
            this.editingCate = true;
            editCateSub = this.adminService.updateMedrecordCategory(this.account, category)
                .subscribe(
                success => {
                    this.editingCate = false;
                    clearTimeout(editCateTO);
                    if (success.status == 200) {
                        this.refreshCategories();
                    } else {
                        console.log("eis" + success.data);
                    }
                    this.closeEditCategoryModel();
                },
                error => {
                    this.editingCate = false;
                    clearTimeout(editCateTO);
                    if (!error.status) {
                        this.showFlashError();
                        this.closeEditCategoryModel();
                    } else {
                        if (error.status == 409) {
                            this.existsCate = true;
                            this.submitEditAttempt = false;
                        } else {
                            this.closeEditCategoryModel();
                        }
                    }
                }
                );
        }
    }

    deletePlace(category: MedicalCategory) {

    }
    // Modal
    windowClickModalHandler(event: any) {
        if (event.target.id == 'add-cate-modal') {
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            let placeModal = document.getElementById('add-cate-modal');
            placeModal.style.display = 'none';
            placeModal.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickModalHandler);
        }
        if (event.target.id == 'edit-cate-modal') {
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            let placeModal = document.getElementById('edit-cate-modal');
            placeModal.style.display = 'none';
            placeModal.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickModalHandler);
        }
    }

    openAddNewCategory() {
        this.existsCate = false;
        this.submitAddAttempt = false;
        let placeModel = document.getElementById('add-cate-modal');
        if (placeModel.getAttribute('data-isopened') == '0') {
            this.newCateForm.controls['name'].setValue('');
            this.newCateForm.controls['name'].markAsPristine();
            document.getElementsByTagName('body')[0].style.overflow = 'hidden';
            placeModel.style.display = 'flex';
            placeModel.setAttribute('data-isopened', '1');
            window.addEventListener('click', this.windowClickModalHandler);
        }
    }

    openEditCategory(cate: MedicalCategory) {
        this.chosenCate = cate;
        this.existsCate = false;
        this.submitEditAttempt = false;
        this.editCateForm.controls['id'].setValue(cate.id);
        this.editCateForm.controls['name'].setValue(cate.name);

        let placeModel = document.getElementById('edit-cate-modal');
        if (placeModel.getAttribute('data-isopened') == '0') {
            document.getElementsByTagName('body')[0].style.overflow = 'hidden';
            placeModel.style.display = 'flex';
            placeModel.setAttribute('data-isopened', '1');
            window.addEventListener('click', this.windowClickModalHandler);
        }
    }

    closeAddCategoryModel() {
        let placeModel = document.getElementById('add-cate-modal');
        if (placeModel.getAttribute('data-isopened') == '1') {
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            placeModel.style.display = 'none';
            placeModel.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickModalHandler);
        }
    }

    closeEditCategoryModel() {
        let placeModel = document.getElementById('edit-cate-modal');
        if (placeModel.getAttribute('data-isopened') == '1') {
            document.getElementsByTagName('body')[0].style.overflow = 'auto';
            placeModel.style.display = 'none';
            placeModel.setAttribute('data-isopened', '0');
            window.removeEventListener('click', this.windowClickModalHandler);
        }
    }
    // End Modal

    trackByID(index: number, item: MedicalCategory) { return item.id; }

    showFlashError() {
        let item = document.getElementById('flash-noti-error');
        item.style.display = 'block';
        setTimeout(() => {
            item.style.display = 'none';
        }, 4000);
    }
}



