import {Component, OnInit, OnDestroy} from "@angular/core";
import {Router, ActivatedRoute} from "@angular/router";
import {REACTIVE_FORM_DIRECTIVES, FormBuilder, FormGroup, FormControl} from "@angular/forms";
import { Subscription } from 'rxjs/Subscription';

import { Account } from '../../account/account.model';
import { Patient, StatusStatistic, AccountRole, AccountStatus } from '../admin.model';
import { AdminService } from '../admin.service';

import { DOMHelper } from '../../shared/helpers';

@Component({
    selector: 'admin-patients',
    template: require('./patientmgt.component.html'),
    styles: [
        require('../../../sass/pages/patients.scss')
    ],
    providers: [FormBuilder]
})
export class PatientMgtComponent implements OnInit, OnDestroy {
    private account: Account;

    private recordsPerPage: number = 20;
    private currentPage: number = 1;
    private totalPages: number = 0;
    private patients: Array<Patient> = [];
    private chosenPatient: Patient = new Patient();
    private statusStat: StatusStatistic = new StatusStatistic();

    private tableDataLoading: boolean = false;
    private statsDataLoading = false;
    private searchingPatient = false;

    private searchForm: FormGroup;
    private getPatientSub: Subscription;
    private sName = '';
    private sStatus = -1;

    constructor(private adminService: AdminService,
        private router: Router,
        private route: ActivatedRoute,
        private builder: FormBuilder) { }

    ngOnInit() {
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (!this.account) {
            this.router.navigate(['/signin']);
        } else {
            this.sName = this.route.snapshot.params['name'];
            this.sStatus = this.route.snapshot.params['status'];
            this.currentPage = +this.route.snapshot.params['page'];
            if (!this.currentPage) this.currentPage = 1;

            this.getStatusStatistic();
            this.getPatients();
        }
        this.setupForm();
    }

    ngOnDestroy() {
        if (this.getPatientSub) this.getPatientSub.unsubscribe();
    }

    private setupForm() {
        let s = this.sStatus || -1;
        let n = this.sName || '';
        this.searchForm = this.builder.group({
            status: new FormControl(s),
            name: new FormControl(n)
        });
    }

    private getPatients(pageNo?: number) {
        let getPatientTO = setTimeout(() => {
            if (this.tableDataLoading && this.getPatientSub && !this.getPatientSub.isUnsubscribed) {
                this.getPatientSub.unsubscribe();
                this.tableDataLoading = false;
                this.showFlashError();
            }
        }, 5000);

        this.patients = [];
        this.searchingPatient = false;
        this.currentPage = pageNo || this.currentPage;
        this.getPatientSub = null;
        this.tableDataLoading = true;
        this.getPatientSub = this.adminService.filterPatients(this.account, this.sName, null, this.sStatus, this.currentPage, this.recordsPerPage).subscribe(
            success => {
                clearTimeout(getPatientTO);
                this.tableDataLoading = false;
                if (success.status == 200) {
                    this.patients = success.data;
                    this.totalPages = Math.ceil(parseInt(success.total) / this.recordsPerPage);

                    let obj = { };
                    if (this.sName) obj['name'] = this.sName;
                    if (this.sStatus && this.sStatus != -1) obj['status'] = this.sStatus;
                    if (this.currentPage > 1) {
                        obj['page'] = this.currentPage;
                    }
                    this.router.navigate(['/admin/patients', obj]);
                } else {
                    console.log("Error in suc" + success.errorMsg);
                }

            },
            error => {
                this.tableDataLoading = false;
                clearTimeout(getPatientTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
        );


        return false;
    }

    getStatusStatistic() {
        let getStatsSub: Subscription;
        let getStatsTO = setTimeout(() => {
            if (this.statsDataLoading && getStatsSub && !getStatsSub.isUnsubscribed) {
                getStatsSub.unsubscribe();
                this.statsDataLoading = false;
                this.showFlashError();
            }
        }, 5000);

        this.statsDataLoading = true;
        let getStats = this.adminService.getAccountStatusStatistic(this.account, AccountRole.PATIENT)
            .subscribe(
            success => {
                this.statsDataLoading = false;
                clearTimeout(getStatsTO);
                if (success.status == 200)
                    this.statusStat = success.data;
                else {
                    console.log("E i s" + success.errorMsg);
                }

            },
            error => {
                this.statsDataLoading = false;
                clearTimeout(getStatsTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }
            }
            );

    }

    searchRecords(data: any, valid: boolean) {
        this.currentPage = 1;
        this.patients = [];
        let obj = {};
        if (data.name) {
            if (data.name != '')
                obj['name'] = data.name;
            this.sName = data.name;
        } else {
            this.sName = '';
        }
        if (data.status) {
            if (data.status != -1)
                obj['status'] = data.status;
            this.sStatus = data.status;
        } else {
            this.sStatus = -1;
        }
        this.router.navigate(['/admin/patients', obj]);
        this.getPatients();
    }

    getAllPatient() {
        this.patients = [];
        this.searchForm.controls['name'].setValue('');
        this.searchForm.controls['status'].setValue(-1);
        this.sName = '';
        this.sStatus = -1;
        this.currentPage = 1;
        this.router.navigate(['/admin/patients']);
        this.getPatients();
    }

    changePatientStatus(patient: Patient) {
        let changeStatusSub: Subscription;
        let changeStatusTO = setTimeout(() => {
            if (changeStatusSub && !changeStatusSub.isUnsubscribed) {
                changeStatusSub.unsubscribe();
                this.showFlashError();
            }
        }, 5000);

        let newStatus = patient.status == AccountStatus.DISABLED ? AccountStatus.ACTIVE : AccountStatus.DISABLED;
        let btnStatus = document.getElementById(`btn-status-${patient.id}`);
        btnStatus.style.opacity = '0.5';
        btnStatus.style.pointerEvents = 'none';
        changeStatusSub = this.adminService.changeStatus(this.account, patient.id, newStatus)
            .subscribe(
            success => {
                clearTimeout(changeStatusTO);
                if (success.status == 200) {
                    patient.status = newStatus;
                    this.getStatusStatistic();
                } else {
                    console.log("E i s" + success.errorMsg);
                }

                btnStatus.style.opacity = '1';
                btnStatus.style.pointerEvents = 'auto';
            },
            error => {
                clearTimeout(changeStatusTO);
                if (!error.status) {
                    this.showFlashError();
                } else {
                    console.log("Error" + error);
                }

                btnStatus.style.opacity = '1';
                btnStatus.style.pointerEvents = 'none';
            }
            );

    }

    trackByID(index: number, item: Patient) { return item.id; }

    showFlashError() {
        let item = document.getElementById('flash-noti-error');
        item.style.display = 'block';
        setTimeout(() => {
            item.style.display = 'none';
        }, 4000);
    }
} 



