import { Account } from "../account/account.model";

export class StatusStatistic {
    activeAccounts: number = 0;
    pendingAccounts: number = 0;
    deactiveAccounts: number = 0;
    totalAccounts: number = 0;
}

export class Place {
    id: string;
    city: string;
    country: string;
}

export class Specialty {
    id: string;
    name: string;
}

export class Doctor {
    id: string = '';
    firstName: string = '';
    lastName: string = '';
    fullName: string = '';
    email: string = '';
    password: string = '';
    dob: string = '';
    sex: number = 0;
    phone: string = '';
    role: number = 3;
    status: number = 2;
    address: string = '';
    photo: string = "data:image/png;base64,";;
    created: string;
    lastModified: string;
    money: number = 0;
    rank: number = 0;
    specialty: Specialty;
    place: Place;
    noOfVoters: number = 0;
    attachment: string;

    constructor() {
        this.specialty = new Specialty();
        this.place = new Place();
    }
}

export class Patient {
    id: string;
    firstName: string = '';
    lastName: string = '';
    fullName: string = '';
    email: string = '';
    password: string;
    dob: string = '';
    sex: number = 0;
    address: string = '';
    role: number = 2;
    status: number = 2;
    phone: string;
    photo: string = "data:image/png;base64,";
    height: number;
    weight: number;
    created: string = '';
    lastModified: string = '';
}


export class MedicalCategory {
    id: string;
    name: string;
    created: string;
    constructor(id?: string, name?: string, desc?: string) {
        this.id = id || '';
        this.name = name || '';
    }
}

export class WorkingExperient {
    id: string;
    description: string;
    images: WEImage[] = [];
}

export class WEImage {
    id: string;
    image64: string;
    imageBlob: Blob;
    size: string;
}

export enum AccountRole {
    ADMIN = 1,
    PATIENT = 2,
    DOCTOR = 3
}

export enum AccountStatus {
    DISABLED = 0,
    PENDING = 1,
    ACTIVE = 2
}


