import { Component, ViewEncapsulation, OnInit } from '@angular/core';
import { ROUTER_DIRECTIVES, Router } from '@angular/router';
import { FORM_PROVIDERS } from '@angular/forms';
import { HTTP_PROVIDERS } from '@angular/http';
import {SigninComponent} from './account/signin.component';
import {AdminComponent} from './admin/admin.component';
import {Account} from "./account/account.model";

import { AdminService } from "./admin/admin.service";
import { Place} from "./admin/admin.model";

import { Http, Response, Headers, RequestOptions } from '@angular/http';
import { Observable } from 'rxjs/Observable';

@Component({
    selector: 'my-app',
    template: require('./app.component.html'),
    styles: [
        require('../sass/main.scss')
    ],
    encapsulation: ViewEncapsulation.None,
    providers: [HTTP_PROVIDERS, FORM_PROVIDERS]
})
export class AppComponent implements OnInit {
    private account: Account;

    constructor(private router: Router, private admin: AdminService, private http: Http) { }

    ngOnInit() {
        this.account = JSON.parse(window.sessionStorage.getItem('account'));
        if (this.account) {
            this.router.navigate(['/admin']);
        } else  {
            this.router.navigate(['/signin']);
        }

        let p = new Place();
        p.city = "HP";
        p.country = "Vietnam"
        // this.createPlace(this.account,p) 
        //     .subscribe(
        //         s => {
        //             console.log('s');
        //         },
        //         e =>{
        //             console.log('e')
        //         }
        //     );
    }

    createPlace(account: Account, data: Place) {
        let headers = new Headers({
            "Accept-Language": "en-US",
            'Content-Type': 'application/json',
            "Email": account.email,
            "Password": account.password
        });
        let options = new RequestOptions({ headers: headers });
        let body = JSON.stringify({
            "City": data.city,
            "Country": data.country
        });
        return this.http.post("http://localhost:46170/api/place", body, options)
            .map((res: Response) => { return res.status})
            .catch((err: any) => { return Observable.throw(err)});
    }

    signOut() {
        window.sessionStorage.setItem('account', null);
        this.router.navigate(['/signin']);
    }

    closeFlashModal() {
        let item = document.getElementById('flash-noti-error');
        item.style.display = 'none';
    }
}



